/* Tabpage  */
.tab-icon {
    background-image: url(../images/icon-tab.png);
    background-position: 0px 2px;
    background-repeat: no-repeat;
    display: inline-block;
    vertical-align: middle;
    width: 60px;
    height: 60px;
    transition: all .3s ease;
    &.phone-pay {
        background-position:00px 2px;
  
        &:hover, &:focus  {
            background-position: -60px 2px;
        }
    }
    &.card-pay {
        background-position: 0px -58px;

        &:hover, &:focus  {
            background-position: -60px -58px;
        }
    }
    &.card-game {
        background-position: 0px -118px;
        &:hover, &:focus  {
            background-position: -60px -118px;
        }
    }
    &.game-pay {
        background-position: 0px -178px;
        &:hover, &:focus  {
            background-position: -60px -178px;
        }
    }
    &.internet-pay {
        background-position: 0px -238px;
        &:hover, &:focus  {
            background-position: -60px -238px;
        }
    }

    &.icon-24 {
        width: 24px;
        height: 24px;
        background-size: 48px;
        &.phone-pay {
            background-position:00px 1px;
      
            &:hover, &:focus  {
                background-position: -23px 1px;
            }
        }
        &.card-pay {
            background-position: 0px -22px;
    
            &:hover, &:focus  {
                background-position: -23px -22px;
            }
        }
        &.card-game {
            background-position: 0px -46px;
            &:hover, &:focus  {
                background-position: -23px -46px;
            }
        }
        &.game-pay {
            background-position: 0px -70px;
            &:hover, &:focus  {
                background-position: -23px -70px;
            }
        }
        &.internet-pay {
            background-position: 0px -93px;
            &:hover, &:focus  {
                background-position: -23px -93px;
            }
        }
    }
}
.tabpage {
    .nav-tabs {
        border-bottom: 2px solid #ddd;
        >li {
            margin-bottom: -2px;
            min-width: 20%;
            margin-left: -1px;
            text-align: center;
            &::before {
                content: ' ';
                position: absolute;
                left: 0;
                bottom: 0;
                right: 0;
                margin: auto;
                width: 0;
                height: 2px;
                background-color: @primary-color;
                transition: all .3s ease;
            }
            &.active,&:hover {
                &::before {
                    width: 100%;
                }
                >a {
                    border-color: transparent;
                }
            }
            >a {
                border-color: transparent;
                display: inline-block;
                min-height: 75px;
                
                padding-left: 65px;
                position: relative;
                >.tab-icon {
                    position: absolute;
                    left: 0;
                    top: 0;
                }
                .sub-title{
                    font-size: small;
                }
                .title{
                    font-size: medium;
                    text-align: left;
                    color: #666;
                }
            }
            a:hover, a:focus, &.active >a {
                background: transparent;                    
                .phone-pay {
                    &{
                        background-position: -60px 2px;
                    }
                }
                .card-pay {
                    & {
                        background-position: -60px -58px;
                    }
                }
                .card-game {
                    & {
                        background-position: -60px -118px;
                    }
                }
                .game-pay {
                    & {
                        background-position: -60px -178px;
                    }
                }
                .internet-pay {
                    & {
                        background-position: -60px -238px;
                    }
                }
            }
        }
    }

}
