.side-order {
    .label {
        display: inline-block;
        font-weight: normal;
        color: #333;
        font-size: inherit;
        vertical-align: middle;
        padding: 5px 0;
    }
    .cell-value {
        float: right;
        padding: 6px 0;
        vertical-align: middle;
    }
    .total-order {
        &, .label {
            font-size: x-large;
        }
    }
    .cell-sum {
        float: right;
        padding: 6px 0;
        vertical-align: middle;
    }
}

