        background-image: url('data:image/png;base64,');
    
}

.method-item {
    min-height: 20px;
    label {
        font-size: inherit;
    }
}
.btn-group {
    margin: auto;
    margin-bottom: 15px;
    position: relative;
    .btn {
        min-width: 120px;
        font-weight: 600;
    }
    .btn-xmd {
        padding: 10px;
    }        
}
.tnap-group {
    >.btn {
        min-width: 110px;
        margin-bottom: 15px;
        margin-right: 15px;
        padding: 16px 20px;
        font-weight: 600;
        &:last-child{
            margin-right: 0;
        }
    }
    img {
        max-width: 100%;
    }
    &.border-active {
        >.btn {
            padding: 6px 12px;
            position: relative;
        }
    }
    .rightfix {
        position: absolute;
        top: 5px;
        right: 5px;
        color: #900;
        z-index: 9;
        background: #fff;
        // border: 1px solid #900;
        // box-shadow: 0 2px 3px #00000038;
        font-size: smaller;
        border-radius: 100%;
        padding: 2px;
    }
}
.form-frontpage {
    label {
        font-weight: normal;
    }
    .form-group {
        position: relative;
    }
    .form-control {
        height: 42px;
        min-width: 245px;
        padding: 10px 12px;        
    }

    .dropdown {
        .caret {
            top: 7px;
            right: 14px;
            position: absolute;
        }
    }
    .dropdown-menu {
        width: 100%;
        padding: 10px 0px;
        max-height: 250px;
        overflow: auto;
        li {
            .clearfix;
            > span, >a {
                padding: 6px 12px;
            }
        }
        .left-drop {
            float: left;
        }
        .right-drop {
            float: right;
        }
    }
}
.telco-icon {
    display: inline-block;
    position: relative;
    min-width: 64px;
    min-height: 32px;
    &::before {
        content: ' ';
        display: block;
        width: 64px;
        height: 32px;
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    &.telco-icon-right {
        &::before {
            left: auto;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    &.icon-viettel {
