.blockDichVu {  
    @media (min-width:@screen-md) {
        &.mainColumn, &.sideColumn {
            float: left;
        }
        &.mainColumn {
            width: 60%;
            margin-right: 15px;
        }
        &.sideColumn {
            width: calc(~'40%' - 15px); 
        }
    }
}
