.blockTitle {
    h1,h2,h3 {
        text-align: left;
        margin-top: 0;
        padding-top: 15px;
        margin-bottom: 15px;
        // padding-left: 15px;  
        // border-left: 2px solid @second-color;
        font-size: 15px;
        text-transform: uppercase;
        line-height: 30px;
        vertical-align: middle;
        span, a {
            color: @primary-color;
            font-weight: 600;
        }
        
        .subTitle {
            font-size: 14px;
            color:#333;
        }
    }
    &.text-center{
        h2,h2,h3 {
            span, a {
                color: #9E9E9E;
                font-weight: 300;
                text-align: center;
                display: block;
            }
        }
    }
}
.darkBlock {
    .blockTitle {
        h1,h2,h3 {
            span, a {
                color: #f5f5f5;
            }
        }
    }
}
 
