.small-blockitem {
    h1,h2,h3,h4 {
        font-size: 13px;
        color: #333;
        font-weight: normal;
    }
} 

.boxTable {
    background: #f5f5f5;
    border: 1px solid #e8e8e8;
    .heading { 
        h1,h2,h3,h4 {
            margin: 0;
            font-weight: 500;
            text-transform: uppercase;
            padding: 15px;
            font-size: 14px;
            text-align: center;
            background: #f5f5f5;
            box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
        }
    }
    .content {
        .title {
            font-size: 14px;
        }
        min-height: 120px;
        padding: 15px;
    }
}
.table-course {
    th,td {
        text-align: center
    }
}
.titleList {
    padding-left: 15px;
    position: relative;
    &:before {
        content: '';
        display: block;
        width: 6px;
        height: 6px;
        position: absolute;
        border-radius: 100%;
        background: @second-color;    
        left: 0;
        top: 4px;
    }
}

