    padding: 0px 0;
    .btn_submit_action{
        margin-top: 15px;
        margin-bottom: 0;
    }
}

// FORM
.form-label {
    padding: 7.25px 0px;
    display: block;
    display: block;
    font-size: 14px;
    white-space: nowrap;
}
#khoahocSelect,#khoahocSelect .dd-select{
    width: 100% !important;
}
.dd-select {
    .dd-selected {
        padding: 0px 10px !important;
        background: #fff;
        box-shadow: inset 0 1px 2px fade(#000,10%);
    }
    .dd-option-image, .dd-selected-image { 
        max-width: 40px !important;
    }
    .dd-selected-text {
        line-height: 30px !important;
        margin-bottom: 0;
    }    
}
.dd-options {
    width: 100% !important;
    .dd-option-text {
        line-height: 30px !important;
    }
}
.supPal {
    .h3 {
       font-size: 14px;
       text-transform: uppercase 
    }
    .text-block {
        &.normal-text {
            &,a,span {
                color: #333;
                font-size: 14px;
            }
            .fa,.icon, i {
                color: @second-color;
            }
        }
        &.color-text{
            &,a {
                color: @second-color;
                font-size: 16px;
            }
        }
        &.hotline {
            &,a {    
                font-size: 22px;
                font-weight: 700;
                letter-spacing: -1px;
                text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
            }
        }
        >span,>a {
            font-size: 16px;
            display: inline-block;
            vertical-align: middle;
            margin:0 10px 10px 0px;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    @media (min-width: @screen-xs) {
        padding: 15px 0 206px
    }
}

.blogItem {
    // padding: 15px;
    img {
        max-width: 100%;
        display: block;
        margin: auto;
    }
    & {
        border: 1px solid #e8e8e8;
        margin-bottom: 15px;
    }
    .title {
        padding: 10px 0;
        display:block;
        color:#333;
        font-weight: 500;
    }
    .info-meta {
        font-size: 11px;
        color: #999;
        margin-bottom: 10px;
        .info-inline {
            margin-right: 10px;
            i {
                margin-right: 5px;
            }
        }
    }

    .detail {
        .clearfix;
        padding:10px; 
        background: #fff;
        position: relative;
        transition: all .3s ease;
    }
    .btn.btn-viewmore {
        background: #ccc;
        border-radius: 0;
        // width: 100%;
        display: inline-block;
        margin: 0;
        padding: 2px 8px; 
        height: auto;
        overflow: hidden;
        transition: all .3s ease;
    }
    &.normalCat {
        .cover {
            display: block;
            min-width: 160px;
            width: 38%;
            float: left;
            margin: 20px 15px;
            position: relative;
            z-index: 1;
            border: 1px solid #fff;
            // box-shadow: @boxShadow;
            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
            img {
                width: 100%;
                display: block;
            }
        }
        .btn.btn-viewmore {
            font-size: 11px;
            color: @second-color; 
            background: rgba(183, 183, 183, 0.2);
            border-radius: @radius;
            &:hover {
                background: @second-color;
                color: #fff;
            }
        }
    }
    &.aniReadmore {
        .title {            
            text-transform: uppercase;
        }
        .btn.btn-viewmore {
            height: 0;
            display: block;
            margin: 0 -10px -10px;
            padding: 0;
            text-transform: uppercase;
        }
        .cover {
            display: block;
            margin-bottom: 0;
            transition: all .3s ease;
        }
        &:hover {
            .cover {
                margin-bottom: -30px;
            }
            .btn.btn-viewmore {
                height: 30px;
                padding: 6px 10px;
            }
        }
    }
}