.section-slider {
    .cover {
        background: #000;   
        img {
            opacity: .35;
        }
        .heading {
            position: absolute;
            left: 0;
            top: 50%;
            right: 0;
            text-align: center;
            transform: translateY(-50%);
            &,a {                
                color: #fff;
            }
            .h1, .h2, .h3, h1, h2, h3 {
                margin-top: 10px;
                margin-bottom: 10px;
                &.dboubeLine {
                    text-transform: uppercase;
                    font-weight: 400;
                    // border:1px solid #fff;
                    border-width: 1px 0 1px 0;
                    display: inline-block;
                    padding: 10px 30px 45px;
                    position: relative;
                    &::before {
                        content: " ";
                        display: block;
                        position: absolute;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        height: 35px;
                        background: transparent url(../images/icon-logo.png) no-repeat center bottom;
                    }
                    //icon-contact-page.png
                    &.contact-icon {
                        &::before {
                            background-image: url(../images/icon-contact-page.png)
                        }
                    }
                }
            }
        }
    }
}

.fullImage {
    margin-bottom: 15px;
    img {
        margin: auto;
        display: block;
        width: 100%;
    }
}
.hotline {
    font-size: 28px;
    font-weight: bold;
}
.box-shadow {    
    box-shadow: @boxShadow;
}
.border-left {    
    border-left: 1px solid #eaeaea;
}
.sidebar {
    // margin-top: 15px;
    padding: 0 15px;
    background: #fff;
    .blockTitle { 
        &.lineHorizontal {
            position: relative;
            &:before {
                content: ' ';
                display: block;
                position: absolute;
                top:50%;
                left: 0;
                width: ~'calc(100% - 15px)';
                height: 1px;
                // background: @second-color;
                background: #eaeaea;
            }
            h1, h2, h3 { 
                position: relative;
            }
        }       
        h1, h2, h3 {
            margin-top: 15px;
            position: relative;
            background: #fff;
            display: inline-block;
            padding: 0px 15px;
            border-left: 0px solid fade(@second-color, 20);            
        }
    }
    .blockContent {
        padding: 15px;
    }
    &.second-color {        
        background: @second-color;
        color: #fff;
        .blockTitle.lineHorizontal {
            &:before {
                background: #fff;
            }
        }
        h1, h2, h3 {
            background: @second-color;
            border-left-color: fade(#fff, 20); 
            &, span, a {
                color: #fff;
            }
        }
        .hotline {
            color:#FFEB3B;
        }
    }
    .blockTitle.gray-color {        
        background: #f5f5f5;
        color: #333;
        &.lineHorizontal {
            &:before {
                background: #333;
            }
        }
        h1, h2, h3 {
            background: #f5f5f5;
            border-left-color: fade(#333, 20); 
            &, span, a {
                color: #333;
            }
        } 
    }
}

