.addressAccordion {
    background: #fff;
    position: relative;
    padding-top: 50px;
    min-height: 193px;
    i, .fa,.icon {
        margin-right: 15px;
    }
    .heading {
        position: absolute;
        top:0;
        left: 0;
        right: 0;
        color: #999;
        &.active-colapsed {
            color: @second-color;
        }
    }
    .info-pg:first-child .heading {
        left: 15px;
        right: 50%;
    }
    .info-pg:last-child .heading {
        right:  15px;
        left: 50%;
        text-align: right;
    }
}

.heading-page {
    background: #f7f5f2;
}
.breadcrumb {
    padding: 8px 0px;
    margin-top: 10px;
    margin-bottom: 10px;
    list-style: none;
    background-color: transparent;
    border-radius: 0;
    a, span {
        display: inline-block;
        margin-right: 5px;
        color: #999;
    }
    a {        
        font-weight: 500;
        color: @primary-color;
    }
}
.page-title {
    font-weight: 700;
    color: #666;
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 24px;
}
.mainpage-wrapper {
    padding-top: 20px;
}